main {
  display: flex;
  background-color: #F5F5F5;
  width: 320px;
  height: 320px;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

button {
  width: 100px;
  color: white;
  font-family: 'Karla';
  font-weight: bold;
  background: #5035FF;
  box-shadow: 0px 3.2px 7.68px rgba(0, 0, 0, 0.18);
  border-radius: 3.81277px;
  border: 0;
  padding: 10px;
  cursor: pointer;
}

.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.dice {
  display: grid;
  grid-template: 30px 30px / 30px 30px 30px 30px 30px;
  gap: 20px;
}

.title {
  font-size: 30px;
  margin: 0;
}

.instructions {
  padding: 0 20px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  margin-top: 0;
  text-align: center;
}