.die {
    display: flex;
    background: #FFFFFF;
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    color: #2B283A;
    font-family: 'Karla';
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 3.81277px;
    cursor: pointer;
}

.held {
    background-color: #59E391;
}